body {
    /* background: #2c3e50; */
	background: rgb(247, 247, 247);
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    /* min-height: 100vh; */

	  font-family: 'Baloo 2', cursive;
}

form {
  padding: 0 2%;
  display: flex;
  flex-direction: column;
  grid-gap: 1.3rem;
  gap: 1.3rem;
}

a {
  text-decoration: none;
  color: black;
}

p {
  display: block;
  -webkit-margin-before: 0em;
          margin-block-start: 0em;
  -webkit-margin-after: 0em;
          margin-block-end: 0em;
  -webkit-margin-start: 0px;
          margin-inline-start: 0px;
  -webkit-margin-end: 0px;
          margin-inline-end: 0px;
}

.button {
  /* display: inline-block; */
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
  background: #4285f4;
  background: teal;
  color: #fff;
  font-size: 18px;
  text-transform: uppercase;
  padding: 15px 20px;
  border: none;
  border-radius: 5px;
  box-shadow: 0px 17px 10px -10px rgba(0, 0, 0, 0.4);
  cursor: pointer;
  transition: all ease-in-out 300ms;
}

.button:hover {
  color: #fff;
  box-shadow: 0px 37px 20px -20px rgba(0, 0, 0, 0.2);
  transform: translate(0px, -10px) scale(1.05);
}


.simple_button {
  padding: 10px;
  border: none;
  color: white;
  font-size: 18px;
  background-color: rgb(207, 52, 52);
  border-radius: 3px;
  cursor: pointer;
  display: flex;
  align-items: center;
  grid-gap: 0.6rem;
  gap: 0.6rem;
}

button:focus {
  border: none;
  outline: none;
}


button:disabled,
button[disabled]{
  background-color: rgb(221, 92, 92);
  cursor: auto;
}

/* ------------- Landing --------------------------- */
.landing_wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.landing {
  display: flex;
  /* justify-content: space-between; */
  grid-gap: 8rem;
  gap: 8rem;
}


.landing_left {
  /* display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; */
}

.landing_text {
  margin-top: 7vh;
}

.landing_text h2 {
  display: block;
  /* font-size: 1.5em; */
  -webkit-margin-before: 0.4em;
          margin-block-start: 0.4em;
  -webkit-margin-after: 0.4em;
          margin-block-end: 0.4em;
  -webkit-margin-start: 0px;
          margin-inline-start: 0px;
  -webkit-margin-end: 0px;
          margin-inline-end: 0px;
  font-weight: bold;
  font-size: 2rem;
}

.landing_text p {
  font-size : 19px;
}

.landing_buttons {
  margin-top: 2vh;
