}
/* -------------------------------- login-------------------------------------- */

.box_element{
	box-shadow:hsl(0, 0%, 80%) 0 0 16px;
	box-shadow:hsl(0, 1%, 48%) 0 0 16px;
	background-color: #fff;
	border-radius: 4px;
	padding: 30px;
}

.login_wrapper {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.add_student_header {
    margin:5vh 0;
    text-align: center;
    font-size: 2.8em;
    color: #1d1d1d;
  }

.add_student_header::after {
      content: "";
      background: rgb(56, 56, 56);
      width: 380px;
      height: 5px;
      border-radius: 5px;
      margin: auto;
      display: grid;
      /* justify-content: center; */
  }
  
.add_student {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  
.formStyle { 
    background-color: rgb(252, 250, 250);
    padding: 20px 0; 
    width: 400px; 
    margin-bottom: 20px; 
    border-bottom-width: 2px; 
    border-bottom-color: #363636; 
    border-bottom-color: var(--form_color); 
    border-top-style: none; 
    border-right-style: none; 
    border-left-style: none; 
	  font-size: 20px;
	  color: #363636;
	  color: var(--form_color);
  }

.form_text {
  display: flex;
  justify-content: space-between;
}

.form_text p {
  font-size: 20px;
  color: rgb(100, 100, 100);
}

.form_text a {
  text-decoration: underline;
  font-size: 20px;
  display: flex;
  text-align: center;
  color: rgb(100, 100, 100);

}

.form_button {
  display: flex;
  justify-content: center;
  align-items: center;
}  
  /*To remove the outline that appears on clicking the input textbox*/
input:focus {
    outline: none;
  }
  
select:focus {
    outline: none;
}

textarea:focus {
  outline: none;
}
  /* To format the placeholder text color */
::-webkit-input-placeholder {
  color: #363636;
  color: var(--form_color);
  font-size: 20px;
}

:-moz-placeholder { /* Firefox 18- */
    color: #363636;
    color: var(--form_color);  
  font-size: 20px;
}

::-moz-placeholder {  /* Firefox 19+ */
    color: #363636;
    color: var(--form_color);  
  font-size: 20px;
}

:-ms-input-placeholder {  
    color: #363636;  
    color: var(--form_color);  
  font-size: 20px;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}

@media screen and (max-width: 780px) { 

    .add_student {
        display: block;
    }
    .add_student_header {
        font-size: 22px;
    }

    .add_student_header::after {
        left: 21%;
        width: 200px;
    }

}

@media screen and (max-width: 450px) { 
    .formStyle {
        width: 90vw;
    }
}

