/* --------------------------------------------- Profile --------------------------------------------- */
.profile_wrapper {
  margin: 3vh 1vw;
  display: flex;
  grid-gap: 2rem;
  gap: 2rem;
  /* overflow: hidden; */
}

.profile_left {
  width: 15%;
  /* flex-basis: 15%; */
  display: flex;
  flex-direction: column;
  grid-gap: 2rem;
  gap: 2rem;
  box-shadow:hsl(0, 1%, 48%) 0 0 16px;
	background-color: #fff;
	border-radius: 4px;
  height: 95vh;
  background-color: rgb(235, 69, 69);
}

.left_top h2 {
  text-align: center;
  color: white;
}

.profile_navs {
  display: flex;
  flex-direction: column;
  grid-gap: 0.2rem;
  gap: 0.2rem;
}

.profile_navs p {
  padding: 10px 10px;
  font-size: 20px;
  cursor: pointer;
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
  align-items: center;
  color: white;
}
.nav_active,.profile_navs p:hover  {
  background-color: rgb(250, 249, 249);
  color:rgb(235, 69, 69) !important ;
}

.profile_right{
  width: 82%;
}

.profile_cards {
  width: 100%;
  margin: 3vh 0;
  /* padding: 0 2%; */
  /* padding:0 5% !important; */
  display: flex;
  justify-content: center;
  align-items: center;
  /* overflow-x: scroll; */
  /* gap: 1.5rem; */
}

.profile_cards_wrapper {
  padding: 0 2%;
  display: flex;
  flex-direction: row;
  overflow-x: scroll; 
  grid-gap: 1.5rem; 
  gap: 1.5rem;
}

.profile_cards_wrapper::-webkit-scrollbar {
	height: 20px;
}

.profile_cards_wrapper::-webkit-scrollbar-thumb {
	background:linear-gradient(
		45deg,
		#06dee1,
		#79ff6c
	);
	border-radius: 10px;
}

.profile_cards_wrapper::-webkit-scrollbar-thumb {
	background:linear-gradient(
		45deg,
		rgb(245, 26, 26),
		rgb(235, 69, 69)
	);
	border-radius: 10px;
}

.small_card {
  min-width: 300px;
  max-width: 300px;
  display: flex;
  grid-gap: 3rem;
  gap: 3rem;
  align-items: center;
  margin: 1vh 0;
  box-shadow:hsl(0, 1%, 48%) 0 0 16px;
	background-color: #fff;
	border-radius: 4px;
  padding: 15px;
}

.profile_form  {
  margin: 10vh 0 3vh 0;
  /* width: 80%; */
  height: 50vh;
  display: flex;
  justify-content: center;
  box-shadow:hsl(0, 0%, 77%) 0 0 16px;
	background-color: #fff;
	border-radius: 4px;
  padding: 30px;
}

.form_top{
  display: flex;
  grid-gap: 2rem;
  gap: 2rem;
}

.form_left, .form_right{
  display: flex;
  flex-direction: column;
  align-items: center;
}

.form_bottom {
  margin-top: 3vh;
}

@media screen and (max-width: 1050px) { 
  .formStyle{
    width: 300px;
  }
}

@media screen and (max-width: 780px) { 
  .form_top{
    display: flex;
    flex-direction: column;
  }

  .profile_form {
    height: 70vh;
    width: 70vw;
  }

  .profile_form {
    padding: 10px;
  }

  .profile_left {
    height: 108vh;
  }
}



